@import url(./components/App.module.css);

:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.2;
  font-weight: 400;

  color-scheme: light dark;
  color: #213547;
  background-color: #ffffff;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  font-weight: 500;
  color: #646cff;
  text-decoration: inherit;
}
a:hover {
  color: #535bf2;
}
p {
  margin: 0;
}
li {
  list-style: none;
}
ul {
  margin-left: 0;
}

body {
  margin: 0;
  display: flex;
  min-width: 320px;
  min-height: 100vh;
}
table {
  border-collapse: separate;
  border-spacing: 0 10px; /* Dodaj odstęp pionowy między wierszami */
}
h1 {
  font-size: 1.8em;
  line-height: 1;
}

button {
  border-radius: 15px;
  border: none;
  padding: 0.5em 2em;
  font-size: 0.8em;
  font-weight: 500;
  font-family: inherit;
  box-shadow: #213547 0px 4px 8px;
  background-color: #213547;

  cursor: pointer;
  transition: border-color 0.25s;
}
button:hover {
  box-shadow: #242424b8 2px 4px 4px inset;
}
button:focus,
button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
  a:hover {
    color: #303367;
  }
  button {
    background-color: #f9f9f9;
  }
}
input {
  background-color: #f5faff;
  color: #213547;
  border: none;
  border-radius: 8px;
  padding-left: 4px;
  outline: transparent;
  margin-left: 3px;
}

table {
  padding: 0;
  margin: 10px 0;
  width: 100%;
}

thead {
  text-align: left;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
  width: 100%;
}
label {
  font-weight: 600;
  padding: 3px;
}
